'use strict';

(function () {

    angular.module("advApp")
        .factory("commonHttpInterceptor", ['$q', '$window', '$rootScope', 'sharedModel', 'eventConstants', 'appConstants',
            function ($q, $window, $rootScope, sharedModel, eventConstants, appConstants) {

                var apiHost = appConstants.apiHost;
                var updateServiceUri = apiHost + "update";

                return{
                    request: function (config) {
                        /*
                         Called before sending a new XHR request.
                         This is a good place where we can manipulate the request parameters.
                         */
                        if (config.url.match(apiHost)) {
                            //TODO uncomment below code when entitlment configured
                            var domainCode = (typeof sharedModel.currentDomain !== "undefined") ? sharedModel.currentDomain.domainCode : sharedModel.defaultDomainCode;
                            var reqToken = {domain: domainCode, soeId: $window.userId};
                            if (config.data) {
                                config.data.requestToken = reqToken;
                            } else {
                                config.data = {requestToken: reqToken};
                            }
                        }
                        return config || $q.when(config);
                    },

                    requestError: function (rejection) {
                        // Called when another request fails.
                        console.log("Got request error");
                        return $q.reject(rejection);
                    },

                    response: function (response) {
                        // Always called before a promise is resolved.
                        console.log("Interceptor response");

                        if (response.config.url.match(updateServiceUri)) {
                            $rootScope.$broadcast(eventConstants.UPDATE_TICKER_COUNT);
                        }

                        if (response.data.result == "SESSION_ERROR") {
                            console.log("INVALID SESSION");
                            $rootScope.$broadcast(eventConstants.SESSION_INVALIDATED);
                        }
                        return response || $q.when(response);
                    },

                    responseError: function (rejection) {
                        /*
                         Called when another XHR request returns with
                         an error status code.
                         */
                        console.log("Got response error");
                        return $q.reject(rejection);
                    }
                };
            }]);
})();
