'use strict';

(function(){

	angular.module("advApp")
		.factory("downloadUtil", ['$window',
			function($window) {

				var downloadUtil = {};

				downloadUtil.oldExport = function(responseData, headerFunction) {
					
					var headerArray = headerFunction();
					console.log("TYPE: ", (typeof responseData));
					var dataUrl = 'data:application/vnd.ms-excel;utf-8,' + btoa(encode_utf8(responseData));
					var a = document.createElement('a');
					a.setAttribute("download", headerArray["x-filename"]);
					a.setAttribute("href", dataUrl);
					a.click();
				};
				
				downloadUtil.downloadToExcel = function (responseData, headerFunction) {
					
					var headerArray = headerFunction();
			        var blob = new Blob([responseData], {
			            type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=utf-8"
			        });
			        var objectUrl = URL.createObjectURL(blob);
			        var a = document.createElement('a');
					a.setAttribute("download", headerArray["x-filename"]);
					a.setAttribute("href", objectUrl);
					a.click();
			        
			    };
				
				downloadUtil.downloadToXML = function(responseData, headerFunction) {
					/*
					var headerArray = headerFunction();
					console.log("TYPE: ", (typeof responseData));
					var dataUrl = 'data:application/xml;utf-8,' + encodeURI(responseData);
					*/
					
					var headerArray = headerFunction();
			        var blob = new Blob([responseData], {
			            type: "application/xml;charset=utf-8"
			        });
			        var dataUrl = URL.createObjectURL(blob);
					
					var a = document.createElement('a');
					a.setAttribute("download", headerArray["x-filename"]);
					a.setAttribute("href", dataUrl);
					a.click();
				};
				
				return downloadUtil;
			}]);

})();
