(function () {

    'use strict';

    angular.module('advApp')

        .config([
            '$stateProvider',
            '$urlRouterProvider',
            '$httpProvider',
            'cfpLoadingBarProvider',
            'IdleProvider',
            'KeepaliveProvider',
            function ($stateProvider, $urlRouterProvider, $httpProvider, cfpLoadingBarProvider, IdleProvider, KeepaliveProvider) {

                //disable the spinner for loading bar
                cfpLoadingBarProvider.includeSpinner = false;

                // configure Idle settings
                IdleProvider.autoResume(true); // Do not resume idle watching on user activity
                IdleProvider.idle(60 * 20); // in seconds
                IdleProvider.timeout(60 * 5); // in seconds
                KeepaliveProvider.interval(60 * 10); // in seconds

                /**
                 * @description Default to "home" state when no route found.
                 */
                $urlRouterProvider.otherwise('/dashboard');


                /**
                 * @description State registration for the application.
                 */
                $stateProvider
                    .state('dashboard', {
                        url: '/dashboard',
                        controller: 'dashboardController',
                        templateUrl: 'app/modules/dashboard/views/dashboard.tpl.html'
                    })
                    .state('wjgrid', {
                        url: '/wijmo-grid',
                        controller: 'wjGridController',
                        controllerAs : 'vm',
                        templateUrl: 'app/modules/shell/views/wj-grid.tpl.html'
                    })
                    .state('add-note', {
                        url: '/add-note',
                        controller: 'noteController',
                        controllerAs : 'vm',
                        templateUrl: 'app/modules/note/views/add-note.tpl.html'
                    })
                    .state('logout', {
                        url: '/logout',
                        views: {
                            content: {
                                templateUrl: 'views/Logout.html'
                            }
                        }
                    });


                $httpProvider.defaults.useXDomain = true;
                $httpProvider.defaults.withCredentials = true;
                //$httpProvider.interceptors.push("commonHttpInterceptor");

            }]);

})();