'use strict';

(function(){

    angular.module('advApp')
    .factory('RendererFactory', ['$timeout','agGridUtils', function($timeout,agGridUtils) {

        var rendererFactory = {};
        /**
			data ==> Actual flat data
			groupingField ==> Field on which we group the data.
			responseField ==> parent field for groupingField

		*/


        rendererFactory.getUISelectRenderer = function(params, html, editVar) {
            var domElement = document.createElement("div");

            if (params.node.hasOwnProperty("parent") || params.node.type == "single") {

                domElement.innerHTML = html;
                domElement.style.width = "100%";
                domElement.style.height = "100%";
                domElement.style.display = "block";

                params.$scope.changeValue = function() {
                    params.$scope[editVar] = false;
                    if (this.$select.selected && this.$select.selected.length > 0) {
                        //var arr = this.$select.selected;
                        var arr = this.$select.selected;
                        var item = arr[arr.length - 1];
                        arr = [];
                        arr.push(item)
                        if (typeof(arr[0]) === "string") {
                            params.$scope.fieldValue = arr[0];
                        } else {
                            params.$scope.fieldValue = arr[0].text;
                        }
                        this.$select.search = params.$scope.fieldValue;
                        this.$select.searchInput[0].value = this.$select.search
                    }
                    params.$scope[editVar] = false;
                    //params.$scope.fieldValue = this.$select.selected.name;
                    //console.log("selected " + this.$select.selected.name)

                }
                params.$scope.$on("uis:blur", function() {
                    console.log("parent blurred")
                })

                params.$scope.$on("uis:close", function(event, value) {
                    console.log("parent closed CLOSE ", value)
                    params.$scope[editVar] = false;
                    params.$scope.fieldValue = value;
                    params.data.domainExceptions["comment"] = value;

                })

                params.$scope.$on("handlebackspace", function(event, value) {
                    console.log("parent closed BACKSPACE ", value)
                    params.$scope[editVar] = false;
                    params.$scope.fieldValue = value;
                    params.data.domainExceptions["comment"] = value;

                })

                params.$scope.$on("onEnter", function(event, value) {
                    console.log("parent onEnter ", value)
                    params.$scope[editVar] = false;
                    params.$scope.fieldValue = value;
                    params.data.domainExceptions["comment"] = value;

                })




                params.$scope.$on("focuslost", function(event, value) {
                    console.log("parent closed focus")
                    params.$scope[editVar] = false;
                    params.$scope.fieldValue = value;
                    params.data.domainExceptions["comment"] = value;

                })


                params.$scope.$on("focuslostonopen", function(event, value) {
                    console.log("parent closed focuslostonopen ", value)
                        //params.$scope[editVar] = false;
                    params.$scope.fieldValue = value;
                    params.data.domainExceptions["comment"] = value;

                })



                params.$scope.someFunction = function($item, $model) {
                    console.log("");
                }

                params.$scope.onBlur = function() {
                    console.log("changed88888888888888888888")
                    params.$scope[editVar] = false;
                }

                params.$scope.$watch(params.$scope[editVar], function(newValue, oldValue) {
                        console.log("changed88888888888888888888")
                    })
                    // $item, $model

                params.$scope.startEditingComments = function() {
                    params.$scope[editVar] = true; // set to true, to show dropdown

                    // put this into $timeout, so it happens AFTER the digest cycle,
                    // otherwise the item we are trying to focus is not visible
                    $timeout(function() {

                        function onBlurEvent( event ){
                            console.log("blur ", event.target);  
                             var target = $( event.target );
                            if(event.target.type == "text" || event.relatedTarget == null  ){
                                params.$scope[editVar] = false;
                                params.refreshCell();
                                document.activeElement.removeEventListener("blur", onBlurEvent);
                                //document.activeElement.removeEventListener("focus", onFocusEvent, true);
                            }
                            else if(event.relatedTarget && event.relatedTarget.tagName === "DIV"){
                                params.$scope[editVar] = false;
                                params.refreshCell();
                                document.activeElement.removeEventListener("blur", onBlurEvent);
                            }
                        }

                        document.activeElement.addEventListener("blur", onBlurEvent);

                        var select = domElement.querySelector('input');
                        if (select) {
                            select.focus();
                            var val = agGridUtils.nestedFieldValueGetter(params);
                            if (typeof(val) === "string") {
                                select.value = (val && val.length > 0) ? val.replace(",", "") : "";
                            } else if (Array.isArray(val)) {
                                if (val && val.length > 0) {
                                    val = val.join("");
                                    select.value = val;
                                } else {
                                    console.log("select")
                                    select.value = "";
                                }

                            }
                            console.log("value", select.value)
                        }
                        //this.$select.focus();
                    }, 0);
                };
            }
            return domElement;
        }

        rendererFactory.gridCellRenderer = function(params, type, datasource) {
            var editIndicator = "editing" + params.data.fieldName;
            var dataSourceName = "dataSource" + params.data.fieldName;
            params.$scope[editIndicator] = false;
            if (type == "dropdown") {
                params.$scope[dataSourceName] = datasource;
                var html = '<span style="width:100%; display: block;" ng-show="!' + editIndicator + '" ng-click="startEditing()">{{data.' + params.colDef.field + '}}&nbsp;&nbsp;&nbsp;</span> ' +
                    '<select ng-cloak  style="width:100%; height:100%; padding:0px;" ng-blur="' + editIndicator + '=false" ng-change="' + editIndicator + '=false" ng-show="' + editIndicator + '" ng-options="item for item in ' + dataSourceName + '" ng-model="data.' + params.colDef.field + '">';
                return getComboBoxRenderer(params, html, editIndicator);
            } 
             if (type == "date") {
                
               /* var html = '<span style="width:100%; display: block;" ng-show="!' + editIndicator + '" ng-click="startEditing()">{{data.' + params.colDef.field + '}}&nbsp;&nbsp;&nbsp;</span> ' +
                '<div class="col-md-6" ng-show="' + editIndicator + '">'+
                    '<p class="input-group">'+
                      '<input datepicker-append-to-body="true" type="text" class="form-control" datepicker-popup="{{format}}" ng-model="dt" '+
                      'is-open="status.opened" min-date="minDate" max-date="maxDate" datepicker-options="dateOptions" date-disabled="disabled(date, mode)" ng-required="true" close-text="Close" />'+
                      '<span class="input-group-btn">'+
                        '<button type="button" class="btn btn-default" ng-click="open($event)"><i class="glyphicon glyphicon-calendar"></i></button>'+
                      '</span>'+
                    '</p>'+
                '</div>'*/
                var html = '<span id="textspan" style="width:100%;  display: block;"  ng-show="!' + editIndicator + '" ng-click="startEditing(event)">'+
                    '{{data.' + params.colDef.field + '}}&nbsp;&nbsp;&nbsp;</span> ' +
                    '<input ng-cloak style="width:100%; height:100%; padding:0px;" type="text" data="date" datepicker-popup="dd-MMMM-yyyy"  ng-model="dt" class="ng-pristine" ng-show="' + editIndicator + '"'+
                        'show-weeks="false" date-disabled="disabled(date, mode)" show-button-bar="true" close-text="Close" datepicker-append-to-body="true" '+
                        'ng-blur="' + editIndicator + '=false" ng-change="' + editIndicator + '=false"/>'
                /*'<input autofocus style="width:100%; height:100%; padding:0px;" ng-show="' + editIndicator + '" ng-hide="!' + editIndicator + '"'+
                    'datepicker-append-to-body="true" type="text" data="date" datepicker-popup="dd-MMMM-yyyy" min="minDate" '+
                    'show-weeks="false" date-disabled="disabled(date, mode)" is-open="status.opened"'+
                    'show-button-bar="true" close-text="Close" id="" ng-model="dt" placeholder="Activity Date To " '+
                    'class="ng-pristine ng-valid ng-touched" style="" ng-click="open()" ng-blur="' + editIndicator + '=false" ng-change="' + editIndicator + '=false">'*/

                return getDateRenderer(params, html, editIndicator);
            } 
            else {

                var html = '<span style="width:100%; display: block;" ng-show="!' + editIndicator + '" ng-click="startEditing()">{{data.' + params.colDef.field + '}}&nbsp;&nbsp;&nbsp;</span> ' +
                    '<input ng-cloak  type="text" ng-blur="' + editIndicator + '=false" style="width:100%; height:100%; padding:0px;"  ng-show="' + editIndicator + '" ng-model="data.' + params.colDef.field + '">';
                return getTextRenderer(params, html, editIndicator)
            }
        }

        function getTextRenderer(params, html, editVar) {
            var domElement = document.createElement("div");

            if (params.node.hasOwnProperty("parent") || params.node.type == "single") {

                domElement.innerHTML = html;
                domElement.style.width = "100%";
                domElement.style.height = "100%";
                domElement.style.display = "block";

                params.$scope.startEditing = function() {
                    params.$scope[editVar] = true; // set to true, to show dropdown
                    $timeout(function() {


                        function onBlurEvent( event ){
                            console.log("blur ", event.target);  
                             var target = $( event.target );
                            if(event.target.type == "text" || event.relatedTarget == null  ){
                                params.$scope[editVar] = false;
                                params.refreshCell();
                                document.activeElement.removeEventListener("blur", onBlurEvent);
                                //document.activeElement.removeEventListener("focus", onFocusEvent, true);
                            }
                            else if(event.relatedTarget && event.relatedTarget.tagName === "DIV"){
                                params.$scope[editVar] = false;
                                params.refreshCell();
                                document.activeElement.removeEventListener("blur", onBlurEvent);
                            }
                        }

                        document.activeElement.addEventListener("blur", onBlurEvent);

                        var input = domElement.querySelector('input');
                        input.focus();
                    }, 0);
                };
            }
            return domElement;
        }


       
       function getDateRenderer(params, html, editVar) {
            var domElement = document.createElement("div");

            if (params.node.hasOwnProperty("parent") || params.node.type == "single") {

                domElement.innerHTML = html;
                domElement.style.width = "100%";
                domElement.style.height = "100%";
                domElement.style.display = "block";

                domElement.querySelector('.ng-pristine').addEventListener('click', function() {
                    console.log("clicked")
                });
                
                domElement.querySelector('#textspan').addEventListener('click', function() {
                    console.log("clicked span");
                    //domElement.querySelector('.ng-pristine').click();
                });


                domElement.querySelector('#textspan').addEventListener('blur', function() {
                    console.log("blur span");
                    //domElement.querySelector('.ng-pristine').click();
                });



                params.$scope.formats = ['dd-MMMM-yyyy', 'yyyy/MM/dd', 'shortDate'];
                params.$scope.format = params.$scope.formats[0];
                params.$scope.opened = false;

                params.$scope.disabled = function(date, mode) {
                    return ( mode === 'day' && ( date.getDay() === 0 || date.getDay() === 6 ) );
                };

                params.$scope.toggleMin = function() {
                    params.$scope.minDate = params.$scope.minDate ? null : new Date();
                };
                params.$scope.toggleMin();
                params.$scope.maxDate = new Date(2020, 5, 22);

                params.$scope.open = function($event) {
                    params.$scope.status.opened = true;
                    this.status.opened = true;
                };

                params.$scope.dateOptions = {
                    formatYear: 'yy',
                    startingDay: 1

                };

                

                params.$scope.status = {
                    opened: false
                };

                
                params.$scope.startEditing = function(event) {
                    params.$scope[editVar] = true; // set to true, to show dropdown
                    var focusElement;
                    $timeout(function() {
                        
                    

                    function onBlurEvent( event ){
                       // console.log("blur ", event.target);  
                         var target = $( event.target );
                        if(event.target.type == "text" || event.relatedTarget == null){
                            params.$scope[editVar] = false;
                            params.refreshCell();
                            params.$scope.status.opened = false;
                            document.activeElement.removeEventListener("blur", onBlurEvent);
                            document.activeElement.removeEventListener("focus", onFocusEvent, true);
                        }
                        else if(event.relatedTarget && event.relatedTarget.tagName === "DIV"){
                                params.$scope[editVar] = false;
                                params.refreshCell();
                                document.activeElement.removeEventListener("blur", onBlurEvent);
                        }
                    }

                    document.activeElement.addEventListener("blur", onBlurEvent);
                    document.activeElement.addEventListener("focus", onFocusEvent, true);

                     function onFocusEvent( event ){
                        focusElement = event.target;
                        //console.log("blur ", event.target);  
                         if(event.target.type == "text"){
                            //params.$scope.status.opened = true;
                         }
                         
                    }


                     
                     

                        //$( "input[type=text]" ).focus();
                    }, 0);
                };
            }
            return domElement;
        }

        function getComboBoxRenderer(params, html, editVar) {
            var domElement = document.createElement("div");

            if (params.node.hasOwnProperty("parent") || params.node.type == "single") {

                domElement.innerHTML = html;
                domElement.style.width = "100%";
                domElement.style.height = "100%";
                domElement.style.display = "block";

                params.$scope.startEditing = function() {
                    params.$scope[editVar] = true; // set to true, to show dropdown

                    // put this into $timeout, so it happens AFTER the digest cycle,
                    // otherwise the item we are trying to focus is not visible
                    $timeout(function() {


                        function onBlurEvent( event ){
                            console.log("blur ", event.target);  
                             var target = $( event.target );
                            if(event.target.type == "text" || event.relatedTarget == null){
                                params.$scope[editVar] = false;
                                params.refreshCell();
                                document.activeElement.removeEventListener("blur", onBlurEvent);
                                //document.activeElement.removeEventListener("focus", onFocusEvent, true);
                            }
                            else if(event.relatedTarget && event.relatedTarget.tagName === "DIV"){
                                params.$scope[editVar] = false;
                                params.refreshCell();
                                document.activeElement.removeEventListener("blur", onBlurEvent);
                            }
                           
                        }

                        document.activeElement.addEventListener("blur", onBlurEvent);

                        var select = domElement.querySelector('select');
                        select.focus();
                    }, 0);
                };
            }
            return domElement;
        }






        return rendererFactory;

    }])

})();