/**
 * @description Service for handling user authentication and session related data.
 * @requires $http, appContants
 * @type AngularJS Service. Registered using module api.
 * @see http://docs.angularjs.org/guide/dev_guide.services.understanding_services
 */


'use strict';

(function () {

    angular.module('advApp')
        .factory('authService', [
            '$http',
            'appConstants',
            function ($http, appConstants) {

                var authenticationService = {};

                /**
                 * Call the server login service with userId.
                 *
                 * @param {String} userId The soe-id of the user trying to log in.
                 * @return {promise} Angular promise which is resolved by the caller.
                 * @public
                 */
                authenticationService.loginUser = function (userId) {
                    $http.defaults.useXDomain = true;
                    /*return $http.post(appConstants.loginServiceURI);*/
                    return $http.post(appConstants.apiHost + appConstants.loginServiceURI);
                };

                /**
                 * Check is the current user is already logged in and has a session present on the server
                 *
                 * @param {String} userId The soe-id of the user trying to log in.
                 * @return {promise} Angular promise which is resovled by the caller.
                 * @public
                 */
                authenticationService.checkSession = function (userId) {
                    $http.defaults.useXDomain = true;
                    return $http.get(appConstants.apiHost + appConstants.validateSessionURI + userId);

                };

                /**
                 * Call the logout service which will invalidate the current session for the user.
                 *
                 * @param {String} userId The soe-id of the user trying to log in.
                 * @return {promise} Angular promise which is resovled by the caller.
                 * @public
                 */
                authenticationService.logoutUser = function (userId) {
                    $http.defaults.useXDomain = true;
                    return $http.get(appConstants.apiHost + appConstants.logoutServiceURI + userId);
                };

                return authenticationService;

            }]);


})();
