'use strict';

(function(){

	angular.module('advApp')
	.factory('GroupedDataGenerator', function(){

		var dataUtil = {};
		/**
			data ==> Actual flat data
			groupingField ==> Field on which we group the data.
			responseField ==> parent field for groupingField

		*/

		dataUtil.createGridData = function(data, isGrouped, groupingField, responseField){
			if(typeof(isGrouped) !== "boolean") {
				throw "isGrouped should be boolean";
			}
			else {
				var currentArray = data;
				if( !isGrouped ){
					return createFlatData(data)
				}
				else{
					return createGroupedData(data, groupingField, responseField)
				}
			}
		}

		function createFlatData(currentArray){
			for (var i = 0; i < currentArray.length; i++) {
				var obj = currentArray[i];
				setObjectMetadata(obj);
				
			}
			return currentArray;

		}

		function setObjectMetadata(obj){
			obj["metadata"] = {};
			obj["metadata"]["responseStatus"] = "";
			obj["metadata"]["responseCode"] = "";
		}


		function createGroupedData (data, groupingField, responseField) {
			//valueField = "recordList"
			var currentArray = data;
			var resultArray = [];

			for (var i = 0; i < currentArray.length; i++) {

				var eObject = {};

				setObjectMetadata(eObject)
				eObject["data"] = currentArray[i];
				//responseField = "regResponses"
				var regResponse = eObject.data[responseField];
				//groupingField = "affectedFields"
				eObject["group"] = (regResponse.hasOwnProperty(groupingField) && regResponse[groupingField].length > 1 ) ? true : false;
				eObject["type"] = (eObject.group) ? "multiple" : "single";

				if(regResponse.affectedFields != undefined && regResponse.affectedFields.length > 1) {

					var affectedFieldsArray = regResponse.affectedFields;
					var childArr = [];
					for (var j = 0; j < affectedFieldsArray.length; j++) {

						var newObject = {};
						newObject["group"] = false;//eObject["group"];
						newObject["type"] = eObject["type"];
						newObject["data"] = affectedFieldsArray[j];
						childArr.push(newObject);
					};

					eObject["children"] = childArr;
				}
				else if(regResponse.affectedFields != undefined && regResponse.affectedFields.length == 1){
					eObject["data"]["fieldName"] = regResponse.affectedFields[0].fieldName;
					eObject["data"]["oldValue"] = regResponse.affectedFields[0].oldValue;
				}

				resultArray.push(eObject);
			};

			return resultArray;
		};

		return dataUtil;

	});


})();
