(function () {

    'use strict';

    angular.module('advApp.ngModules', ['ngRoute', 'ngAnimate', 'ngSanitize', 'ngIdle']);
    angular.module('advApp.ngUI', ['ui.router', 'ui.bootstrap', 'ui.date', 'ui.select', 'ui.select2']);//, 'ui.select', 'ui.select2'
    angular.module('advApp.lib', ['wj']);//'toastr',
    angular.module('advApp.directives', [ 'angular-loading-bar']);//'toolbarButtons',
    angular.module('advApp.modules', ['dashboard', 'shell']);
    angular.module('advApp', ['advApp.ngModules', 'advApp.ngUI', 'advApp.lib', 'advApp.directives', 'advApp.modules']);
})();







(function () {

    'use strict';

    angular.module('advApp')

        .config([
            '$stateProvider',
            '$urlRouterProvider',
            '$httpProvider',
            'cfpLoadingBarProvider',
            'IdleProvider',
            'KeepaliveProvider',
            function ($stateProvider, $urlRouterProvider, $httpProvider, cfpLoadingBarProvider, IdleProvider, KeepaliveProvider) {

                //disable the spinner for loading bar
                cfpLoadingBarProvider.includeSpinner = false;

                // configure Idle settings
                IdleProvider.autoResume(true); // Do not resume idle watching on user activity
                IdleProvider.idle(60 * 20); // in seconds
                IdleProvider.timeout(60 * 5); // in seconds
                KeepaliveProvider.interval(60 * 10); // in seconds

                /**
                 * @description Default to "home" state when no route found.
                 */
                $urlRouterProvider.otherwise('/dashboard');


                /**
                 * @description State registration for the application.
                 */
                $stateProvider
                    .state('dashboard', {
                        url: '/dashboard',
                        controller: 'dashboardController',
                        templateUrl: 'app/modules/dashboard/views/dashboard.tpl.html'
                    })
                    .state('wjgrid', {
                        url: '/wijmo-grid',
                        controller: 'wjGridController',
                        controllerAs : 'vm',
                        templateUrl: 'app/modules/shell/views/wj-grid.tpl.html'
                    })
                    .state('add-note', {
                        url: '/add-note',
                        controller: 'noteController',
                        controllerAs : 'vm',
                        templateUrl: 'app/modules/note/views/add-note.tpl.html'
                    })
                    .state('logout', {
                        url: '/logout',
                        views: {
                            content: {
                                templateUrl: 'views/Logout.html'
                            }
                        }
                    });


                $httpProvider.defaults.useXDomain = true;
                $httpProvider.defaults.withCredentials = true;
                //$httpProvider.interceptors.push("commonHttpInterceptor");

            }]);

})();
(function () {

    'use strict';

    angular.module('advApp')

        .run([
            '$templateCache',
            '$rootScope',
            '$window',
            'globalServiceDelegate',
            'sharedModel',
            'eventConstants',
            'appConstants',
            'Idle',
            function ($templateCache, $rootScope, $window, globalServiceDelegate,
                      sharedModel, eventConstants, appConstants, Idle) {

                // start watching when the app runs. also starts the Keepalive service by default.
                Idle.watch();

                var userId = $window.userId;
                $rootScope.appInitialized = true;

            }]);

})();
