/**
 * @ngdoc function
 * @name Dashboard Controller
 * @description
 * AngularJS controller for the sidebar navigation which would dispatch events to load new tabs.
 *
 *
 * @see http://docs.angularjs.org/guide/controller
 *
 */
(function () {

    'use strict';

    angular.module("shell")

        .controller('noteController', [
            '$rootScope',
            '$scope',
            '$log',
            'appConstants',
            function ($rootScope, $scope, $log, appConstants) {

                var countries, data = [], self;

                self = this;
                $scope.title = 'Wijmo Grid';

                (function () {

                    // generate some random data
                    countries = 'US,Germany,UK,Japan,Italy,Greece'.split(',');
                    for (var i = 0; i < 100; i++) {
                        data.push({
                            id: i,
                            country: countries[i % countries.length],
                            date: new Date(2014, i % 12, i % 28),
                            amount: Math.random() * 10000,
                            active: i % 4 == 0
                        });
                    }
                })();

                $scope.data =  data;// add data array to scope
            }]);
})();
