/**
 * @description Service implementations for session utility functions.
 * @requires $http, appContants
 * @type AngularJS Service. Registered using module api.
 * @see http://docs.angularjs.org/guide/dev_guide.services.understanding_services
 */

'use strict';

(function(){

    angular.module('advApp')
            .factory('sessionUtils', ['$http', 'sharedModel', 'appConstants',
        function($http, sharedModel, appConstants) {

        var sessionUtils = {};
        var sModel = sharedModel;
        
        /**
         * @description Authenticate user be checking if the user info contains proper entitlements.
         * @returns {Boolean}
         */
        sessionUtils.authenticateUser = function(user) {
            console.log("authenticate user");
            var resultFlag = false;
            var domains = user.domains;
            
            if(_.has(user, "domains") && _.size(domains) > 0) {

                var domain = _.findWhere(user.domains, {domainCode:sharedModel.defaultDomainCode});

                if(typeof domain !== "undefined") {

                    sModel.setCurrentDomain(domain);
                    var roles = domain.roles;

                    if(_.has(domain, "roles") && _.size(roles) == 1) {

                        // IMPORTANT: THERE SHOULD ALWAYS BE ONLY ONE ROLE FOR A USER PER DOMAIN.
                        var role = roles[0];
                        var modules = role.modules;
                        sModel.setCurrentRole(role);

                        if(_.has(role, "modules") && _.size(modules) > 0) {
                        	
                        	sModel.setModuleList(modules);
                            for (var i = 0; i < modules.length; i++) {

                                resultFlag =  _.has(modules[i], "privilege");
                                if(!resultFlag){
                                    break;
                                }
                            };
                        }
                        else {
                            console.log("No modules found for current user");
                        }
                    }
                    else {
                        console.log("No roles found for current user");
                    }
                }
                else {
                    console.log("No default domain found for current user");
                }
            }

            if(resultFlag) {
                sModel.setCurrentUser(user);
            } else {
                sModel.reset();
            }
            
            return resultFlag;
        };
        
        
        /**
         * @description To look up the user role in user info.
         * @param {type} codeVal The role code to look for.
         * @returns {Object} Role object if found
         */
        sessionUtils.getUserRole = function(codeVal){
            // return current user role.
            return _.findWhere(sModel.userRoles, {roleCode:codeVal});
        };
        
        
        /**
         * @description To look up the user privilege for a particular module based on current user entitlements.
         * @param {type} moduleName The module code to look for.
         * @returns {Object} Privilege object if found
         */
        sessionUtils.getPrivilegeByModule = function(moduleName) {
        	
        	var result = undefined;
        	var module = _.findWhere(sharedModel.currentModules, {moduleCode:moduleName});
        	if(module != undefined){
        		 result =  module.privilege;
        		 console.log("PRIV: ", result[0].privilegeCode);
        	}
        	
        	return result[0];
        };
        
        
        sessionUtils.hasReadWritePermission = function(moduleName) {
        	console.log("HRW: ", sessionUtils.getPrivilegeByModule(moduleName).privilegeCode == appConstants.readWrite);
        	return sessionUtils.getPrivilegeByModule(moduleName).privilegeCode == appConstants.readWrite;
        };
        
        
        /**
         * @description Check if the user is logged in.
         * @returns {Boolean}
         */
        sessionUtils.isUserLoggedIn = function(){
          return (sModel.currentUser !== null && sModel.currentUser !== undefined);  
        };
        
        sessionUtils.isSessionValid = function(){
            return false;
        };

        return sessionUtils;

    }]);



})();